.App{
    background-color:var(--Primary-Cream);
    display:flex;
    place-items: center;
    min-height: 100vh;
    min-width:100%;
  }

  .Container{
    background-color: var(--White);
    display: grid;
    grid-template-columns: 45% 45%;
    border-radius: 10px;
    align-items: center;
    margin: 0 auto;
    width:45%;
    height: 70%;
    gap:1.5rem;
    overflow: hidden;
  }


.DeskImg
{
 max-width: 100%;
 height: 100%;

}

.MobaImg{
    display: none;
    max-width: 100%;
    height: 100%;
}

@media screen and (max-width:500px){
    .Container{
      margin: 1rem auto;
      width:80%;
      height: auto;
      grid-template-columns: 100%;
    }
    .DeskImg{
      display:none;
    }
    .MobaImg{
      display: block;
    }

}