.Details{
    display:flex;
    flex-direction: column;
    gap:1.4rem;
    margin: 1em 0;
}

.Preview{
    font-family: "Fraunces";
    color:var(--Neutral-Dark-Grayish-Blue);
    text-transform: uppercase;
    letter-spacing: 5px;
    font-size: 14pt;
    font-weight: var(--Font-Weight500);
}

.Heading{
    font-weight: var(--Font-Weight700);
    line-height: 1;

}

.PriceSection{
    display: flex;
    align-items: center;
    gap:1rem;
}

.PriceSection h1{
    font-weight: var(--Font-Weight700);
    color: var(--Primary-Dark-Cyan);
    font-family: "Fraunces";
}

.PriceSection span{
    color: var(--Neutral-Dark-Grayish-Blue);
    text-decoration: line-through;
}

.Paragraph{
    color: var(--Neutral-Dark-Grayish-Blue);
    font-weight: var(--Font-Weight500);
  
}

.AddIteamButton{
    color: var(--White);
    font-weight: var(--Font-Weight700);
    display: flex;
    align-items: center;
    justify-content: center;
    gap:1rem;
    height:3rem;
    outline: none;
    border: 0;
    border-radius: 10px;
    background-color: var(--Primary-Dark-Cyan);
    transition: 0.7s;
    
}

.AddIteamButton:hover{
    background-color:var(--Primary-Dark-Cyan1);
    cursor: pointer;
}

@media screen and (max-width:500px){
   .Details{
    padding: 1rem;
   }

}